.texts {
  margin-left: 15px;
  font-family: Roboto, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  letter-spacing: normal;
}

.app {
  display: inline-block;
  vertical-align: top;
  width: 250px;

  color: rgb(50, 60, 50);
  text-decoration: none;

  padding: 5px 3px 5px 7px;
  margin: 10px 0px 3px 15px; 
  border: 1px dashed black;
  background: -webkit-radial-gradient(73% 73%, rgba(115, 255, 190, 0.75), white);
  background: -o-radial-gradient(73% 73%, rgba(115, 255, 190, 0.75), white);
  background: radial-gradient(73% 73%, rgba(115, 255, 190, 0.75), white);
  /*alternative:*/  /*background: linear-gradient(to top, white, rgba(115, 255, 190, 0.75));*/

  -webkit-transition: all .5s ease-in;

  -o-transition: all .5s ease-in;

  transition: all .5s ease-in;
}

.app:hover{
  border-radius: 10px;
  background: rgba(115, 255, 190, 0.5);
}

.app:active{
  -webkit-transition: color .1s ease-in;
  -o-transition: color .1s ease-in;
  transition: color .1s ease-in;
  color: blue;
}

#sideBar{
  background: rgb(63, 81, 181);
  -webkit-box-shadow: 0px 3px 5px grey;
          box-shadow: 0px 3px 5px grey;
  color: white;

  padding: 23px;
  margin: 0px 0px 7px 0px;

  font-family: Roboto, sans-serif;
  font-size: 1.25rem;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;

  display: -ms-flexbox;

  display: flex;
  position: relative;
  -ms-flex-align: center;
      align-items: center;
  top: 0;
  left: 0;
}

#sideBar .barContent {
  position: absolute;
  right: 10px;
}


